.header {
  border-bottom: 1px solid var(--utility-neutral-20);
}

.container {
  align-items: center;
  background: var(--utility-neutral-10);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: auto;
  max-width: var(--layout-max-width);
}

.homeLink {
  text-decoration: none;
}
.homeLink img {
  display: block;
  padding: var(--spacing-xs);
  max-height: 50px;
}

.menuButton {
  background: none;
  border: none;
  cursor: pointer;
  display: none;
  padding: 0;
  margin: 0 var(--spacing-s);
  z-index: 10;
}
@media (max-width: 768px) {
  .menuButton {
    display: block;
  }
}